import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: () => import( '../views/Home')
  },
  {
    path: '/',
    name: 'login',
    component: () => import( '../views/Login')
  },
  {
    path: '/exam',
    name: 'exam',
    component: () => import( '../views/Exam')
  },
  {
    path: '/result',
    name: 'result',
    component: () => import( '../views/Result')
  }
]

const router = new VueRouter({
  routes
})

export default router
